import { Component, OnInit, OnDestroy, ViewContainerRef } from '@angular/core';
import { ActivatedRoute, Router} from "@angular/router";
import { BlogService } from '../blog.service';
import { BlogHttpService} from '../blog-http.service';
import { Location } from "@angular/common";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

@Component({
  selector: 'app-blog-view',
  templateUrl: './blog-view.component.html',
  styleUrls: ['./blog-view.component.css'],
  providers: [Location]
})
export class BlogViewComponent implements OnInit{

  public  currentBlog;


  constructor(private _route:ActivatedRoute, private router: Router, private blogService:BlogService, public blogHttpService: BlogHttpService, private toastr: ToastsManager, vcr: ViewContainerRef, private location: Location) {
    this.toastr.setRootViewContainerRef(vcr);
   }

  ngOnInit() {
    let myBlogId = this._route.snapshot.paramMap.get('blogId');
    console.log(myBlogId);
    this.blogHttpService.getSingleBlogInformation(myBlogId).subscribe(

      data =>{
        console.log(data)
        this.currentBlog=data["data"]      
      },
      error =>{
        console.log("some error occurred")
        console.log(error.errorMessage)
      }
    )
  }

  public deleteThisBlog(): any{
    this.blogHttpService.deleteBlog(this.currentBlog.blogId).subscribe(

      data =>{
        console.log(data);
        this.toastr.success('Blog deleted successfully', 'Success!');
        setTimeout(() =>{
          this.router.navigate(['/home']);
        },1000)
      },
      error =>{
        console.log("some error occurred")
        console.log(error.errorMessage)
        this.toastr.error('some error occurred', 'Error')
      }
    )
  }

  goBackToPreviousPage():any{
    this.location.back()
  }

}



