import { Injectable } from '@angular/core';

//importing Http client to make the request

import {HttpClient, HttpErrorResponse} from '@angular/common/http';

//import observale related code
import {Observable} from "rxjs/Observable";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';

@Injectable()
export class BlogHttpService {

  public allBlogs;
  public currentBlog;
  public baseUrl = 'https://blogapp.edwisor.com/api/v1/blogs'
  private authToken = 'ODdjNjkyNDc0NjZkMGRhYWQ0MTBiMDk4OGUyOGM1ZTAwNDk5NTRjZThkNDgzOWFhZDBmMGY2ODQ1NzY2NDM5ODliZTRhODJlMDA1ZjRkMTVlODI2MTVlZTA4OWQwNzAxMDQzNTFhYjQ5ZjI4NDU4MjgyNmMyODAwMWVjODAzOGZiNw=='

  constructor(private _http:HttpClient) {
    console.log('Blog Http Service was called')
   }

   //exception handler
   private handleError(err: HttpErrorResponse){
     console.log("Handle error Http calls")
     console.log(err.message)
     return Observable.throw(err.message)
   }

   public getAllBlogs():any {
    let myResponse = this._http.get(this.baseUrl+'/all?authToken=' + this.authToken)
    console.log(myResponse)
    return myResponse;
  }

  //method to get the current blog using the blogId
   public getSingleBlogInformation(currentBlogId): any {
    let myResponse = this._http.get(this.baseUrl +'/view'+ '/' + currentBlogId + '?authToken=' + this.authToken)
    return myResponse
  }

  public createBlog(blogData): any {
    let myResponse = this._http.post(this.baseUrl+'/create?authToken=' + this.authToken, blogData)
    console.log(myResponse)
    return myResponse
  }

  public deleteBlog(blogId): any{
    let data={}
    let myResponse = this._http.post(this.baseUrl + '/' + blogId + '/delete?authToken=' + this.authToken ,data)
    return myResponse
  }

  public editBlog(blogId, blogData): any{
    let myResponse = this._http.put(this.baseUrl + '/' + blogId + '/edit' + '?authToken=' + this.authToken, blogData)
    return myResponse
  }

}



